* {
    margin: 0;
    padding: 0;
    border: 0;
    background: transparent;
}
body {
    font-family: sans-serif;
    font-size: 16px;
}
.menu-wrapper {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%,-50%);
        -ms-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
    width: 100%;
}

.menu-choice {
    text-align: center;
    height: 100px;
    width: 100%;
    text-align: center;
}
.menu-choice > a {
    position: relative;
    font-size: 2em;
    color: black;
    text-decoration: none;
    line-height: 100px;
}
.menu-choice > a:before {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 3px;
    background: black;
    visibility: hidden;
    border-radius: 5px;
    -webkit-transform-origin: left;
        -ms-transform-origin: left;
            transform-origin: left;
    -webkit-transform: scaleX(0);
        -ms-transform: scaleX(0);
            transform: scaleX(0);
    -webkit-transition: .25s linear;
    -o-transition: .25s linear;
    transition: .25s linear;
}
.menu-choice > a:hover:before {
    visibility: visible;
    -webkit-transform: scaleX(1);
        -ms-transform: scaleX(1);
            transform: scaleX(1);
}


